import React, { useRef, useLayoutEffect } from "react";
import { gsap } from "gsap";

const AnimatedCircles = () => {
  const containerRef = useRef(null);

  useLayoutEffect(() => {
    const ctx = gsap.context(() => {
      const circles = gsap.utils.toArray(".circle");

      circles.forEach((circle, index) => {
        const anim = gsap.fromTo(
          circle,
          { scale: 0.8, opacity: 0.5 },
          {
            scale: 1,
            opacity: 1,
            duration: 1,
            repeat: -1,
            yoyo: true,
            delay: index * 0.2,
            ease: "power1.inOut",
          }
        );
        return () => anim.kill(); // Clean up animation for unmount
      });
    }, containerRef);

    return () => ctx.revert(); // Clean up GSAP context on component unmount
  }, []);

  return (
    <div className="container" ref={containerRef} style={styles.container}>
      <div className="circle" style={styles.circle}></div>
      <div className="circle" style={styles.circle}></div>
      <div className="circle" style={styles.circle}></div>
    </div>
  );
};

const styles = {
  container: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    height: "100vh",
    backgroundColor: "#282c34",
    overflow: "hidden",
  },
  circle: {
    width: "50px",
    height: "50px",
    margin: "10px",
    borderRadius: "50%",
    backgroundColor: "white",
  },
};

export default AnimatedCircles;
