import React, { useLayoutEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "../styles/scroll-effect.scss";

gsap.registerPlugin(ScrollTrigger);

const PortfolioScrollEffect = () => {
  const containerRef = useRef(null);

  useLayoutEffect(() => {
    const container = containerRef.current;

    gsap.to(container, {
      y: () => container.scrollWidth, // Scroll horizontally
      ease: "none",
      scrollTrigger: {
        trigger: container,
        start: "top top",
        end: () => `bottom top`, // Length of the container
        scrub: true,
        // pin: true,
      },
    });

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => trigger.kill()); // Clean up
    };
  }, []);

  return (
    <div className="portfolio-container">
      <div className="portfolio-slides" ref={containerRef}>
        <h1 className="slide">DEV STUFF</h1>
      </div>
    </div>
  );
};

export default PortfolioScrollEffect;
