import { useLayoutEffect, useRef } from "react";
import { gsap } from "gsap";
import "../styles/header.scss";
import { ScrollTrigger } from "gsap/all";

export const Header = () => {
  const subtitle = useRef(null); // Reference to the h3 element
  const titleRef = useRef(null); // Reference to the h3 element
  const btnRef = useRef(null); // Reference to the h3 element

  useLayoutEffect(() => {
    const text = " DESIGN DEVELOP DEPLOY";
    const chars = "ABCDEFGRSTUVWXYZ1234567890!@#$%^&*";
    const duration = 3; // Total duration of the scrambling animation

    const scrambleText = (element, text, chars, duration) => {
      const scrambledText = text
        .split("")
        .map(() => chars[Math.floor(Math.random() * chars.length)]);
      let index = 0;

      const interval = setInterval(() => {
        scrambledText[index] = text[index]; // Reveal correct character
        element.textContent = scrambledText.join("");

        index++;
        if (index >= text.length) {
          clearInterval(interval); // Stop animation when done
          // Fade in the text with GSAP
          gsap.to(element, { opacity: 1, duration: 1 });
        }
      }, (duration / text.length) * 300);
    };

    // Initially hide the text
    const ctx = gsap.context(() => {
      gsap.from(titleRef.current, {
        opacity: 0, // Zoom in
        duration: 1, // 15 seconds for one cycle
        ease: "power1.inOut", // Smooth easing
      });
      gsap.from(btnRef.current, {
        delay: 1, // Delay the animation
        opacity: 0, // Zoom in
        duration: 1, // 15 seconds for one cycle
        ease: "power1.inOut", // Smooth easing
      });
      gsap.from(subtitle.current, {
        delay: 1, // Delay the animation
        opacity: 0, // Zoom in
        duration: 1, // 15 seconds for one cycle
        ease: "power1.inOut", // Smooth easing
        onStart: () => {
          scrambleText(subtitle.current, text, chars, duration);
        },
      });
      gsap.to(titleRef.current, {
        y: titleRef.current.scrollHeight / 2, // Scroll horizontally
        ease: "none",
        scrollTrigger: {
          trigger: titleRef.current,
          start: "top top",
          end: `bottom top`, // Length of the container
          scrub: true,
        },
      });
    });

    return () => ctx.revert();
  }, []);

  return (
    <header className="header">
      <div ref={titleRef} className="header__content">
        <h2 className="white">TAYLORDEV</h2>
        <h1 className="white">DIGITAL DESIGN DEVELOPMENT</h1>
        <div className="header__subtitle">
          <h3 ref={subtitle} className="white">
            DESIGN DEVELOP DEPLOY
          </h3>
        </div>
        <div ref={btnRef} className="header__actions">
          <button className="main-btn">MY WORK</button>
        </div>
      </div>
    </header>
  );
};
