import React, { useRef } from "react";
import { useGSAP } from "@gsap/react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import "../styles/works-text.scss";

gsap.registerPlugin(ScrollTrigger);

const WorksTextEffect = () => {
  const containerRef = useRef();
  const textRef = useRef();

  useGSAP(
    () => {
      gsap.to(".works-text", {
        y: -500,
        scrollTrigger: {
          trigger: textRef.current,
          start: "top center",
          end: "bottom top",
          scrub: true,
        },
      });
    },
    { scope: containerRef }
  ); // Scope the selectors to the container

  return (
    <div ref={containerRef} className="works-text-container">
      <h1 ref={textRef} className="works-text" data-speed="1">
        Works
      </h1>
      <div className="next-component"></div>
    </div>
  );
};

export default WorksTextEffect;
