import React, { useRef } from "react";
import { gsap } from "gsap";
import { Draggable } from "gsap/Draggable";
import "../styles/carousel.scss";

gsap.registerPlugin(Draggable);

const carouselData = [
  {
    heading: "Design",
    text: "Crafting intuitive and visually stunning designs is at the heart of what I do. Whether you need a sleek modern look or something playful and vibrant, I ensure your website not only looks fantastic but also aligns with your brand identity. Every design is tailored to engage your audience and leave a lasting impression.",
  },
  {
    heading: "Development",
    text: "I bring designs to life with clean, efficient, and responsive code. From dynamic functionality to seamless user experiences, I develop websites that are robust, fast, and optimised for all devices. Whether it’s creating from scratch or improving existing sites, I focus on functionality without compromising style.",
  },
  {
    heading: "Landing Pages",
    text: "Need a high-converting landing page? I create focused, action-driven pages designed to captivate your audience and deliver results. Perfect for campaigns, product launches, or lead generation, each landing page is crafted to achieve your goals.",
  },
  {
    heading: "E-commerce",
    text: "Looking to sell online? I build e-commerce websites that make shopping effortless for your customers. From product pages to secure checkout processes, I develop scalable and user-friendly online stores tailored to your business needs.",
  },
  {
    heading: "Hosting",
    text: "Let me take the hassle out of hosting. I offer reliable and secure hosting solutions to keep your website up and running smoothly. With proactive monitoring and maintenance, your site will always perform at its best.",
  },
  {
    heading: "Support",
    text: "Your website deserves care and attention even after it’s live. I provide ongoing support to keep things updated, secure, and functioning perfectly. Whether it’s troubleshooting, updates, or adding new features, I’m here to help your site evolve with your business.",
  },
];

const Carousel = () => {
  const wrapperRef = useRef(null);
  const containerRef = useRef(null);

  React.useLayoutEffect(() => {
    const slides = gsap.utils.toArray(".carousel__card");
    const slideWidth = containerRef.current.offsetWidth / 3; // Three slides visible
    const totalSlides = slides.length;

    // Duplicate slides for infinite loop
    // const duplicateSlides = [...slides, ...slides];
    // gsap.set(wrapperRef.current, { x: -slideWidth + 1 }); // Offset for centering the first visible slides

    // // Update width dynamically
    // gsap.set(wrapperRef.current, {
    //   width: `${duplicateSlides.length * slideWidth}px`,
    // });
    gsap.set(slides, {
      width: `${slideWidth}px`,
    });

    Draggable.create(wrapperRef.current, {
      type: "x",
      bounds: { minX: -(totalSlides * slideWidth), maxX: 0 },
      inertia: true,
      dragResistance: 0.2,
      onDragEnd() {
        const closestSlide = Math.round(this.x / slideWidth) * slideWidth;
        gsap.to(wrapperRef.current, {
          x: closestSlide - 100,
          duration: 0.5,
          ease: "power2.out",
          //   onComplete: () => {
          //     if (closestSlide <= -(totalSlides * slideWidth)) {
          //       gsap.set(wrapperRef.current, { x: -slideWidth });
          //     } else if (closestSlide >= 0) {
          //       gsap.set(wrapperRef.current, {
          //         x: -slideWidth * (totalSlides - 3),
          //       });
          //     }
          //   },
        });
      },
    });
  }, []);

  return (
    <div className="carousel" ref={containerRef}>
      <div className="carousel__wrapper" ref={wrapperRef}>
        {[...carouselData, ...carouselData].map((item, index) => (
          <div className="carousel__card" key={index}>
            <h3 className="carousel__heading">{item.heading}</h3>
            <p className="carousel__text">{item.text}</p>
          </div>
        ))}
      </div>
      <div className="carousel__dots">
        {carouselData.map((_, index) => (
          <span className="carousel__dot" key={index}></span>
        ))}
      </div>
    </div>
  );
};

export default Carousel;
